import logoEt from './et/logo.png'
import headerEt from './et/header.jpg'
import et1 from './et/1.png'
import et2 from './et/2.png'
import et3 from './et/3.png'
import et4 from './et/4.png'
import et5 from './et/5.png'
import et6 from './et/6.png'
import et7 from './et/7.png'

import logoPac from './pac/logo.png'
import headerPac from './pac/header.png'
import pac1 from './pac/1.png'
import pac2 from './pac/2.png'
import pac3 from './pac/3.png'
import pac4 from './pac/4.png'
import pac5 from './pac/5.png'
import pac6 from './pac/6.png'

const getLang = () => {
  const urlLength = window.location.pathname.length
  const actualLang = window.location.pathname.substring(window.location.pathname.lastIndexOf('/'), urlLength)
  return (actualLang === '/en' ?
    english
  : french)
}

const english = [
  {
    title: 'PAC',
    description: `PAC is an application that aims to improve the business relationships with customers.`,
    technologies: 'React, Redux, Laravel / Lumen, MySQL',
    avatar: logoPac,
    header: headerPac,
    images: [pac1, pac2, pac3, pac4, pac5, pac6]
  },
  {
    title: 'Eliot Trans',
    description: `Eliot Trans is a package delivery company working in Switzerland and Kosovo. A tailored solution has been developed for this company, including an application meeting the business needs and a public web page for parcel tracking.`,
    technologies: 'Vue.js, PHP, MySQL, Progressive Web Application',
    avatar: logoEt,
    header: headerEt,
    images: [
      et1, et2, et3, et4, et5, et6, et7
    ]
  }
]

const french = [
  {
    title: 'PAC',
    description: `PAC est une application qui a pour but d'améliorer les relations 
    commerciales avec les clients.`,
    technologies: 'React, Redux, Laravel / Lumen, MySQL',
    avatar: logoPac,
    header: headerPac,
    images: [pac1, pac2, pac3, pac4, pac5, pac6]
  },
  {
    title: 'Eliot Trans',
    description: `Eliot Trans est une entreprise de transport de marchandises reliant
    la Suisse et le Kosovo. Une solution sur mesure a été conçue pour l'entreprise, comprenant une application répondant aux besoins métiers 
    et une page web publique pour le suivi des colis.`,
    technologies: 'Vue.js, PHP, MySQL, Progressive Web Application',
    avatar: logoEt,
    header: headerEt,
    images: [
      et1, et2, et3, et4, et5, et6, et7
    ]
  }
]

export default getLang


