import React from 'react'
import styles from './Contact.scss'
import Container from 'atoms/Container'
import SecondTitle from 'atoms/SecondTitle'

const Contact = () => {
  return (
    <footer className={styles.Contact}>
      <Container>
        <SecondTitle id="contact-title">Contact</SecondTitle>
        <div className="row">
          <div className="column">
            <div className="info">
              <p>Fabio Avelino</p>
              <p>
              <a href="mailto:fabioavelinop@gmail.com">fabioavelinop@gmail.com</a>
              </p>
              <p>
              <a href="tel:0787483395">078 748 33 95</a>
              </p>
            </div>
          </div>
        </div>
      </Container>
    </footer>
  )
}

export default Contact


