import React, { Component } from 'react'
import PropTypes from 'prop-types'


const getPositionXY = e => {
  e.persist()
  if (e.type === 'touchend') {
    const { pageX, pageY } = e.changedTouches[0]
    return { x: pageX, y: pageY }
  }
  const { pageX, pageY } = e.touches[0]
  return { x: pageX, y: pageY }
}

class Swipe extends Component {
  static PropTypes = {
    onSwipeStart: PropTypes.func,
    onSwipeMove: PropTypes.func,
    onSwipeLeft: PropTypes.func,
    onSwipeRight: PropTypes.func,
    onSwipeCancel: PropTypes.func,
    className: PropTypes.string,
    distanceForSwipe: PropTypes.number
  }

  static defaultProps = {
    className: '',
    onSwipeStart: () => {},
    onSwipeMove: () => {},
    onSwipeLeft: () => {},
    onSwipeRight: () => {},
    onSwipeCancel: () => {},
    distanceForSwipe: 40
  }

  getDifference = ({x, y}) => {
    const { x: startX, y: startY } = this.swipeStart
    const difference = { 
      x: x - startX,
      y: y - startY
    }
    return difference
  }

  handleOnTouchStart = (e) => {
    const { x, y } = getPositionXY(e)
    this.swipeStart = { x, y }
    this.props.onSwipeStart({x, y})
  }

  handleOnTouchMove = e => {
    const { x, y } = getPositionXY(e)
    this.props.onSwipeMove(this.getDifference({x, y}))
  }

  handleOnTouchEnd = (e) => {
    const { onSwipeLeft, onSwipeRight, onSwipeCancel, distanceForSwipe } = this.props
    const { x } = this.getDifference(getPositionXY(e))
    if (x > 0 && x > distanceForSwipe) {
      onSwipeRight()
      return
    }
    if (x < 0 && Math.abs(x) > distanceForSwipe ) { // absolute value of x for comparison with a positive number
      onSwipeLeft()
      return
    }
    onSwipeCancel()
  }

  render () {
    const { children, className } = this.props
    return (
      <div 
        className={className}
        style={{overflow: 'hidden'}}
        onTouchStart={this.handleOnTouchStart}
        onTouchMove={this.handleOnTouchMove}
        onTouchEnd={this.handleOnTouchEnd}
      >
        {children}
      </div>
    )
  }
}

export default Swipe


