import React, { Component } from 'react'
import styles from './Project.scss'
import getLang from 'translate'

class Project extends Component {
  onClickProject = () => {
    const { onClickProject, images } = this.props
    onClickProject(images)
  }

  render () {
    const {title, avatar, description, header, technologies} = this.props
    return (
      <article className={styles.Project} onClick={this.onClickProject}>
        <header>
          <div className="img" style={{backgroundImage: `url(${header}`}} />
        </header>
        <div className={styles.Avatar} style={{backgroundImage: `url(${avatar}`}} />
        <main>
          <div className={styles.Title}>{title}</div>
          <div className={styles.Subtitle}>{getLang()('projects')('description')}</div>
          <p>{description}</p>
          <div className={styles.Subtitle}>{getLang()('projects')('realize')}</div>
          <p>{technologies}</p>
        </main>
      </article>
    )
  }
}

export default Project


