const getLang = () => {
  const urlLength = window.location.pathname.length
  const actualLang = window.location.pathname.substring(window.location.pathname.lastIndexOf('/'), urlLength)
  return (actualLang === '/en' ?
  (category) => (term) => english[category][term]
  : (category) => (term) => french[category][term])
}

const french = {
  navbar: {
    about: 'A propos',
    projects: 'Projets',
    contact: 'Contact',
    lang: 'EN'
  },
  header: {
    title: 'Je suis un développeur full stack de Genève.'
  },
  about: {
    title: 'A propos',
    first: "De la conception de l'interface utilisateur à la mise en place de la base de données sur un serveur, je suis en mesure de réaliser toutes les tâches nécessaires à l'accomplissement de mes missions.",
    second: "Pour cela, je suis sans cesse en apprentissage de nouvelles méthodologies et technologies afin de connaître et choisir la meilleure solution."
  },
  projects: {
    title: 'Projets',
    description: 'Description',
    realize: 'Réalisé avec'
  }
}

const english = {
  navbar: {
    about: 'About',
    projects: 'Projects',
    contact: 'Contact',
    lang: 'FR'
  },
  header: {
    title: "I'm a full stack developer from Geneva."
  },
  about: {
    title: 'About',
    first: "From the design of the user interface to the database install on a server, I'm able to carry out all the necessary tasks for the accomplishment of my missions.",
    second: "To realize this, I'm constantly learning new methodologies and technologies in order to know and choose the best solution."
  },
  projects: {
    title: 'Projects',
    description: 'Description',
    realize: 'Realize with'
  }
}

export default getLang


