import React, { Component } from 'react'
import ProjectsGrid from 'molecules/ProjectsGrid'
import Carousel from 'features/Carousel'
import getLang from 'projects'

class ProjectsGridC extends Component {
  state = {
    showCarousel: false,
    images: []
  }

  onClickClose = e => {
    this.setState({showCarousel: false})
  }

  onClickProject = images => {
    this.setState({showCarousel: true, images})
  }


  render () {
    const { showCarousel, images } = this.state
    return (
      <div>
        <ProjectsGrid 
          projects={getLang()}
          onClickProject={this.onClickProject}
        />
        {showCarousel && <Carousel onClickClose={this.onClickClose} images={images} />}
      </div>
    )
  }
}

export default ProjectsGridC



