/* https://www.sitepoint.com/smooth-scrolling-vanilla-javascript/ */
function elmYPosition(eID) {
  const elm = document.getElementById(eID)
  let y = elm.offsetTop
  let node = elm
  while (node.offsetParent && node.offsetParent !== document.body) {
      node = node.offsetParent
      y += node.offsetTop
  }
  return y
}

export default function smoothScroll(target) {
  const duration = 400
  const margin = 95
  const start = window.pageYOffset
  const stop = elmYPosition(target) - margin
  const distance = stop > start ? stop - start : start - stop

  let timeStart, timeElapsed

  window.requestAnimationFrame(function(time) {
    timeStart = time
    loop(time)
  })

  function loop(time) {
    timeElapsed = time - timeStart
    const nextY = easeInOutQuad(timeElapsed, start, distance, duration)
    if (stop > start) {
      window.scrollTo(0, nextY)
    } else {
      window.scrollTo(0, start - (nextY - start))
    }

    if (timeElapsed < duration)
      window.requestAnimationFrame(loop)
    else
      end()
  }

  function end() {
    if (stop > start) {
      window.scrollTo(0, start + distance)
    } else {
      window.scrollTo(0, start - distance)
    }
  }

  // Robert Penner's easeInOutQuad - http://robertpenner.com/easing/
  function easeInOutQuad(t, b, c, d) {
    t /= d / 2
    if (t < 1) return c / 2 * t * t + b
    t--
    return -c / 2 * (t * (t - 2) - 1) + b
  }
}


