import React, { Component } from 'react'
import styles from './Carousel.scss'
import Swipe from './Swipe'
import Prev from './components/Control/Prev'
import Next from './components/Control/Next'
import Close from './components/Control/Close'
import Slide from './components/Slide'
import Container from './components/Container'

class Carousel extends Component {
  preventClose = e => {
    e.stopPropagation()
  }

  render () {
    const {
      onClickClose,
      onSwipeStart,
      onSwipeMove,
      onSwipeLeft,
      onSwipeRight,
      onSwipeCancel,
      style,
      images
    } = this.props
    return (
      <section className={styles.Carousel} onClick={onClickClose}>
        <Container onClick={this.preventClose}>
            <Close onClick={onClickClose} />
            <Prev onClick={onSwipeRight} />
            <Next onClick={onSwipeLeft} />
            <Swipe
              className={styles.Swipe}
              onSwipeStart={onSwipeStart}
              onSwipeMove={onSwipeMove}
              onSwipeLeft={onSwipeLeft}
              onSwipeRight={onSwipeRight}
              onSwipeCancel={onSwipeCancel}
            >
              {images.map((image, index) => (
                <Slide key={index} style={style} image={image} alt={index+1} />
              ))}
            </Swipe>
        </Container>
      </section>
    )
  }
}

export default Carousel



