import React from 'react'
import styles from './Projects.scss'

import BackgroundPrimary from 'atoms/BackgroundPrimary'
import Container from 'atoms/Container'
import SecondTitle from 'atoms/SecondTitle'
import ProjectsGridC from 'containers/ProjectsGridC'
import getLang from 'translate'

const Projects = () => {
  return (
    <section>
      <BackgroundPrimary withTop className={styles.Projects}>
        <Container>
          <SecondTitle white id="projects-title">{getLang()('projects')('title')}</SecondTitle>
          <ProjectsGridC />
        </Container>
      </BackgroundPrimary>
    </section>
  )
}

export default Projects


