import React, { Component } from 'react'
import smoothScroll from 'lib/scrolling'
import styles from './MenuItem.scss'

class MenuItem extends Component {
  onClickItem = e => {
    const { goTo } = this.props
    if (goTo === 'language') {
      const urlLength = window.location.pathname.length
      const actualLang = window.location.pathname.substring(window.location.pathname.lastIndexOf('/'), urlLength)
      const url = window.location.origin
      if (actualLang === '/fr') {
        window.location.href = url + window.location.pathname.substring(0, window.location.pathname.lastIndexOf('/')) + '/en'
      } else if (actualLang === '/en') {
        window.location.href = url + window.location.pathname.substring(0, window.location.pathname.lastIndexOf('/')) + '/fr'
      } else {
        window.location.href = window.location.href + 'en'
      }
      return
    }
    smoothScroll(`${goTo}-title`)
  }

  render () {
    return (
      <li onClick={this.onClickItem} className={styles.MenuItem}>{this.props.children}</li>
    )
  }
}

export default MenuItem



