import React from 'react'
import styles from './About.scss'
import Container from 'atoms/Container'
import SecondTitle from 'atoms/SecondTitle'
import TextContainer from 'atoms/TextContainer'
import getLang from 'translate'

const About = () => {
  return (
    <section className={styles.About}>
      <Container>
        <SecondTitle id="about-title">{getLang()('about')('title')}</SecondTitle>
        <TextContainer className={styles.TextContainer_alignToRight}>
          <p>{getLang()('about')('first')}</p>
          <p>{getLang()('about')('second')}</p>
        </TextContainer>
      </Container>
    </section>
  )
}

export default About


