import React from 'react'
import MenuItem from 'atoms/MenuItem'
import styles from './NavbarMenu.scss'
import getLang from 'translate'

const NavbarMenu = ({onClickItem, onClickLanguage}) => (
	<ul className={styles.NavbarMenu}>
    <MenuItem goTo="about" onClickItem={onClickItem}>{getLang()('navbar')('about')}</MenuItem>
    <MenuItem goTo="projects" onClickItem={onClickItem}>{getLang()('navbar')('projects')}</MenuItem>
    <MenuItem goTo="contact" onClickItem={onClickItem}>{getLang()('navbar')('contact')}</MenuItem>
    <MenuItem goTo="language" onClickItem={onClickLanguage}>{getLang()('navbar')('lang')}</MenuItem>
  </ul>
)

export default NavbarMenu



