import React, { Component } from 'react'
import Carousel from './Carousel'

const getTransformWithVendorPrefix = (transform) => ({
  transform,
  WebkitTransform: transform,
  MsTransform: transform,
  MozTransform: transform
})

class CarouselContainer extends Component {
  state = {
    style: {},
    currentImage: 0
  }

  onSwipeMove = ({x}) => {
    const { currentImage } = this.state
    const transform = `translateX(calc(${currentImage * -100}% + ${x}px))`
    this.setState({
      style: getTransformWithVendorPrefix(transform)
    })
  }

  doSwipe = () => {
    const percent = this.state.currentImage * -100
    const transform = `translateX(${percent}%)`
    this.setState({
      style: {
        transition: 'transform .3s ease-in-out',
        ...getTransformWithVendorPrefix(transform)
      }
    })
  }

  onSwipeLeft = () => {
    let imageToDisplayNow = this.state.currentImage + 1
    const maxSize = this.props.images.length
    if (imageToDisplayNow >= maxSize) {
      imageToDisplayNow = 0
    }
    this.setState({currentImage: imageToDisplayNow}, () => {
      this.doSwipe()
    })
  }

  onSwipeRight = () => {
    let imageToDisplayNow = this.state.currentImage - 1
    const maxSize = this.props.images.length
    if (imageToDisplayNow < 0) {
      imageToDisplayNow = maxSize - 1
    }
    this.setState({currentImage: imageToDisplayNow}, () => {
      this.doSwipe()
    })
  }

  onSwipeCancel = () => {
    this.doSwipe()
  }

  render () {
    const { onClickClose, images } = this.props
    const { style } = this.state
    return (
        <Carousel 
          onClickClose={onClickClose}
          onSwipeMove={this.onSwipeMove}
          onSwipeLeft={this.onSwipeLeft}
          onSwipeRight={this.onSwipeRight}
          onSwipeCancel={this.onSwipeCancel}     
          images={images}     
          style={style}
        />
    )
  }
}

export default CarouselContainer


