import React from 'react'
import classNames from 'lib/classNames'
import styles from './BackgroundPrimary.scss'

const BackgroundPrimary = ({className, withTop = false, children}) => {
  const allClassNames = classNames((
    withTop ?
    classNames(styles.BackgroundPrimary, styles.BackgroundPrimaryWithTop)
    : styles.BackgroundPrimary
  ), className)
	return <div className={allClassNames}>{children}</div>
}

export default BackgroundPrimary



