import React from 'react'
import classNames from 'lib/classNames'
import styles from './SecondTitle.scss'

const SecondTitle = ({children, id, white = false}) => {
	const className = (white ? 
		classNames(styles.SecondTitle, styles.white)
		: styles.SecondTitle)
	return <h2 id={id} className={className}>{children}</h2>
}

export default SecondTitle



